/* ---------- TITLE BAR ---------- */
#title-bar {
  position: sticky;
  top: 0;
  background-color: white;
  padding: 20px 0;
  border-bottom: 2px solid lightgray;
  /* display: grid; */
  /* grid-template-columns: 33% 33% 33%; */
  display: flex;
  justify-content: space-between;
  box-shadow: 0 0 4px lightgray;
}

/* ---------- STORE NAME ---------- */

.store-name-wrapper {
  display: flex;
  flex: start;
  margin: 0 30px;
}

.store-name {
  font-size: 30px;
  color: dimgray;
  margin: 0 40px;
  white-space: nowrap;
}

/* ---------- NAV ---------- */
.nav-wrapper {
  display: flex;
  justify-content: center;
}

/* ---------- SEARCH ---------- */
.search-bar-wrapper {
  display: flex;
  flex: end;;
  width: 100%;
  min-width: 300px;
  margin: 0 30px;
}

.search-bar-container {
  display: flex;
  align-items: center;
  width: 100%;
  position: relative;
}

#search-bar {
  height: 40px;
  width: 100%;
  border: 2px solid lightgray;
  border-radius: 50px;
  padding-left: 40px;
  font-size: 18px;
  color: gray;
  outline: none;
}

#search-bar:hover {
  border: 2px solid skyblue;
  outline: none;
}
#search-bar:focus + .fa-magnifying-glass {
  color: rgb(101, 183, 216);
}

.fa-magnifying-glass {
  position: absolute;
  left: 14px;
  color: gray;
}

@media all and (max-width: 1500px) {
  
  #title-bar .store-name {
    margin: 0 0 0 20px;
  }

}

@media all and (max-width: 1070px) {
  
  #title-bar {
    display: flex;
    flex-direction: column;
    padding: 0;
    justify-content: center;
    align-items: center;
  }

}


@media all and (max-width: 480px) {
  
  #title-bar .store-name {
    font-size: 24px;
    margin: 0;
    display: none;
  }
  
  #nav .drink-link {
    margin: 0;
  }

  #nav .drink-link img {
    display: none;
  }

  
  #search-bar {
    margin: 8px;
  }
}

